var that;
export default {
	data() {
		return {
			tabCurrentIndex: 0,
			machine_config: '',
			machine_status: '',
			machine_id: '',
			scrollTop: 0,
			old: {
				scrollTop: 0,
			},
			navList: [{
					state: 0,
					text: '服务器配置',
					orderList: []
				},
				{
					state: 1,
					text: '服务器状态',
					orderList: []
				},
				{
					state: 2,
					text: '服务器记录',
					orderList: []
				}
			]
		};
	},
	onLoad(options) {
		that = this;
		this.machine_id = options.machine_id;
		this.tabCurrentIndex = 0; // 页面显示是默认选中第一个
		uni.request({
			url: this.url + 'usermachineinfo/',
			method: 'GET',
			data:{
				machine_id:this.machine_id,
				methods:1
			},
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success(res) {
				that.machine_config = res.data.data;
			}
		});
		uni.request({
			url: this.url + 'usermachineinfo/',
			method: 'GET',
			data:{
				machine_id:this.machine_id,
				methods:2
			},
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success(res) {
				that.machine_status = res.data.data;
			}
		});
	},
	methods: {
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		tabClick(index) {
			that.tabCurrentIndex = index;
		},
		backFront(){
			console.log('back')
			uni.navigateBack({
				delta:1
			})
		}
	}
};
